.hero {
  background-image: url("../Assets/homev2.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 70vh;
  width: 100%;
}
.hero-left {
  line-height: 1.1;
  padding-top: 100px;
}
.hero-left h2 {
  color: rgb(216, 160, 166) !important ;
  font-size: 18px;
}

.hero-left p {
  color: #221e1e;
  font-size: 70px;
  font-weight: bold;
  filter: drop-shadow(5px 1px 2px rgb(102, 100, 100));
  font-family: "Montserrat", sans-serif;
  padding-left: 200px;
}
.hero-hand-icon {
  display: flex;
  align-items: center;
  gap: 20px;
}
.hero-hand-icon img {
  width: 70px;
}
.hero-latest-btn {
  filter: drop-shadow(4px 6px 12px black);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  /* font-family:cursive; */
  width: 350px;
  height: 80px;
  border-radius: 15px;
  margin-top: 20px;
  background: #00b241;
  color: white;
  font-size: 24px;
  font-weight: 500;
  cursor: pointer;
  margin-top: 80px;
  margin-left: 200px;
}
.hero-latest-btn img {
  width: 18px;
}

.hero-right img {
  width: 450px;
}

.kenburns-bottom {
  -webkit-animation: kenburns-bottom 5s ease-out both;
  animation: kenburns-bottom 5s ease-out both;
}
.shadow-core {
  filter: drop-shadow(4px 6px 12px black);
}

.fade-in {
  -webkit-animation: fade-in 3.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
  animation: fade-in 3.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

