.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 1px 3px -2px black;
    padding: 15px 70px;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo img{
    width: 200px;
    height: 100px;
}
.nav-logo p{
    font-size: 28px;
    font-weight: 600;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 40px;
    font-size: 19px;
    font-weight: 500;
}

.nav-menu li{
    display: flex;
    color:#221e1e;
    filter: drop-shadow(3px 1px 2px rgb(102, 100, 100));
    flex-direction: column;
    /* font-family:cursive; */
    align-items: center;
    justify-content: center;
    gap: 3px;
}

.nav-menu li a {
    color:rgb(3, 3, 3) !important ;
    font-size: 22px;
    font-weight: bold;
}
.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    color:rgb(216, 160, 166);
}
.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 30px;
}

.nav-login-cart button{
    width: 130px;
    height: 45px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color:rgb(216, 160, 166);
    font-size: 20px;
    font-weight: 500;
    background: white;
    cursor: pointer;
}
.nav-login-cart button:active{
    background: #f3f3f3;
}
.nav-login-cart img{
    width: 35px;
}
.nav-cart-count{
    width: 18px;
    height: 18px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -30px;
    margin-left: -40px;
    font-size: 14px;
    background: red;
    color: white;
}
.nav-dropdown{
    display: none;
}

.usa{
    background-image: url("../Assets/usa3.png");
    background-repeat: no-repeat;
    background-size:cover; 
    background-position: center;
}
.col{
    background-image: url("../Assets/col2.png");
    background-repeat: no-repeat;
    background-size:cover; 
    background-position: center;
}

/* -------- media query---------- */
@media(max-width:900px){
    .navbar{
        padding: 15px 40px;
    }
    .nav-menu{
        gap: 15px;
    }
}

@media(max-width:750px){
    .navbar{
        padding: 15px 20px;
    }
    .nav-login-cart{
        gap: 15px;
    }
    .nav-dropdown{
        display: block;
        width: 30px;
        rotate: -90deg;
        transition: 0.5s;
    } 
    .nav-menu{
        display: none;
        height: auto;
        width: 100%;
        position: absolute;
        background-color: white;
        justify-content: center;
        top: 60px;
        left: 0;
    }
    .nav-menu-visible{
        display: flex;
        height: auto;
        flex-direction: column;
        z-index: 1;
    }
    .nav-dropdown.open{
        transform: rotate(90deg);
    }
    .nav-cart-count{
        margin-left: -25px;
    }
}

@media(max-width:500px){
    .navbar{
        padding: 15px 15px;
    } 
    .nav-menu li a {
        color:rgb(3, 3, 3) !important ;
        font-size: 15px;
        font-weight: bold;
        display: flex;
    }

    .nav-logo img{
        width: 80px;
        height: 50px;
    }
    .nav-logo p{
        font-size: 18px;
    }
    .nav-menu-visible{
        height: auto;
        top: 60px;
    }
    .nav-login-cart{
        transform: scale(0.8);
    } 
    .nav-login-cart{
        gap: 10px;
    }
}


display: flex;
flex-direction: column;
z-index: 1;